import * as d3 from 'd3';
import './tooltip.scss';


function floatingTooltip(tooltipId, width) {
  // Local variable to hold tooltip div for manipulation in other functions.
  const tt = d3.select('body').append('div')
    .attr('class', 'bubble-tooltip')
    .attr('id', tooltipId)
    .style('pointer-events', 'none');

  if (width)
    tt.style('width', width)

  hideTooltip(); // initially hide
    
  function hideTooltip () {
    tt.style('opacity', 0.0);
  }

  function showTooltip (content, event) {
    tt.style('opacity', 1.0)
      .html(content)

    updatePosition(event);
  }

  function updatePosition(event) {
    const xOffset = 20;
    const yOffset = 10;

    const ttw = tt.style('width');
    const tth = tt.style('height');

    const wscrX = window.scrollX;
    const wscrY = window.scrollY;

    const curX = (document.getElementById) ? event.clientX + wscrX : event.pageX;
    const curY = (document.getElementById) ? event.clientY + wscrY : event.pageY;

    let ttleft = ((curX - wscrX + xOffset * 2 + ttw) > window.innerWidth)
      ? curX - ttw - xOffset * 2 
      : curX + xOffset;
    if (ttleft < wscrX + xOffset)
      ttleft = wscrX + xOffset;

    let tttop = ((curY - wscrY + yOffset * 2 + tth) > window.innerWidth)
      ? curY - tth - yOffset * 2 
      : curY + yOffset;
    if (tttop < wscrY + yOffset)
      tttop = wscrY + yOffset;

    tt
      .style('top', `${tttop}px`)
      .style('left', `${ttleft}px`);    
  }

  return {
    showTooltip,
    hideTooltip,
    updatePosition,
  };
}

const tooltip = floatingTooltip('gates_tooltip', 240);
export default tooltip;