import React from 'react';

export default function YearsTitles({ yearCenters }) {
  return (
    <g className="yearsTitles">
      {
        Object.keys(yearCenters).map(year =>
          <text
            key={year}
            x={yearCenters[year].x}
            y={50}
            fontSize="35"
            textAnchor="middle"
            alignmentBaseline="middle"
          >
            {
              year
            }
          </text>)
      }
    </g>
  )
}