import React from 'react'
import * as d3 from 'd3';
import tooltip from './tooltip';

const fillColor = d3.scaleOrdinal().domain(['low', 'medium', 'high']).range(['#525b7a', '#beccde', '#5aa2bc']);

class Bubbles extends React.Component {
  constructor(props) {
    super(props)
    this.state = { 
      g: null
    }

    const { forceStrength, center } = this.props
    this.simulation = d3.forceSimulation()
      .velocityDecay(0.2)
      .force('x', d3.forceX().strength(forceStrength).x(center.x))
      .force('y', d3.forceY().strength(forceStrength).y(center.y))
      .force('charge', d3.forceManyBody(this.charge.bind(this)))
      .on('tick', this.ticked.bind(this))
      .stop()
  }

  charge(d) {
    return -this.props.forceStrength * (d.radius ** 2.0)
  }

  ticked() {
    this.state.g.selectAll('.bubble')
      .attr('cx', d => d.x)
      .attr('cy', d => d.y)
  }


  
  shouldComponentUpdate = () => false

  // TODO: replace it with other method (deprecated)
  componentWillReceiveProps(nextProps) {
    if (nextProps.data !== this.props.data)
      this.renderBubbles(nextProps.data)
    if (nextProps.groupByYear !== this.props.groupByYear)
      this.regroupBubbles(nextProps.groupByYear)
  }
  


  renderBubbles(data) {
    const bubbles = this.state.g.selectAll('.bubble')
      .data(data, d => d.id)

    // EXIT
    bubbles.exit().remove()

    // ENTER
    const bubblesE = bubbles.enter().append('circle')
      .classed('bubble', true)
      .attr('r', 0)
      .attr('cx', d => d.x)
      .attr('cy', d => d.y)
      .attr('fill', d => fillColor(d.group))
      .attr('stroke', d => d3.rgb(fillColor(d.group)).darker())
      .attr('stroke-width', 2)
      // TODO: ADD TOOLTIP
      .on('mouseover', showDetail)
      .on('mouseout', hideDetail)

      bubblesE.transition().duration(2000)
        .attr('r', d => d.radius)
        .on('end', () => {
          this.simulation.nodes(data)
            .alpha(1)
            .restart()
        })
  }
  
  regroupBubbles (groupByYear) {
    const { forceStrength, yearCenters, center } = this.props;
    if (groupByYear)
      this.simulation
        .force('x', d3.forceX().strength(forceStrength).x(d => yearCenters[d.year].x))
        .force('y', d3.forceY().strength(forceStrength).y(d => yearCenters[d.year].y));        
    else
      this.simulation
        .force('x', d3.forceX().strength(forceStrength).x(center.x))
        .force('y', d3.forceY().strength(forceStrength).y(center.y));
    
    this.simulation.alpha(1).restart();
  }


  onRef (ref) {
    this.setState({ g: d3.select(ref) }, () => this.renderBubbles(this.props.data))
  }

  render() {
    return ( 
      <g ref={this.onRef.bind(this)} className="bubbles" />
    )
  }
}
 
export default Bubbles;

export function showDetail(d) {
  d3.select(this).attr('stroke', 'black');

  const content = `<span class="name">Title: </span><span class="value">${d.name}</span><br/>`
    + `<span class="name">Amount: </span><span class="value">$${d.value}</span><br/>`
    + `<span class="name">Year: </span><span class="value">${d.year}</span>`;

  tooltip.showTooltip(content, d3.event);
}


export function hideDetail(d) {
  d3.select(this).attr('stroke', d3.rgb(fillColor(d.group)).darker());
  
  tooltip.hideTooltip();
}