import React from 'react';
import axios from 'axios';
import he from 'he';
import { UtilHtml } from '../../utils';
import moment from 'moment';

const VYoutubeSearch = () => {
  const [searchInfo, setSearchInfo] = React.useState({query: '', pageToken: '', bannedChannels: ''});
  const [list, setList] = React.useState([]);
  const [history, setHistory] = React.useState([]); // {query: '', pageToken: '', bannedChannels: ''}

  React.useEffect(() => {
    let last_history = localStorage.getItem('youtube_history');
    if (!last_history)
      return;
    
    setHistory(JSON.parse(last_history));
  }, [setHistory]);


  const requestSearch = async (e) => {
    let pageToken = searchInfo.pageToken;
    if (history.length) {
      const lastHistory = history.slice().pop();
      if (
        (lastHistory.query !== searchInfo.query) ||
        (lastHistory.bannedChannels !== searchInfo.bannedChannels)
      ) {
        pageToken = '';
      }
    }

    let result_list;
    try {
      // https://developers.google.com/youtube/v3/getting-started#quota
      result_list = await axios.get('https://www.googleapis.com/youtube/v3/search', {
        params: {
          pageToken,
          key: process.env.REACT_APP_GOOGLE_API_KEY,
          q: searchInfo.query,
          part: 'snippet',
          fields: 'nextPageToken,items(id(videoId),snippet(channelTitle,title,publishTime,thumbnails(medium(url))))',
          maxResults: 50,
          order: 'date',
          type: 'video'
        }            
      });
    } catch (e) {
      console.log(e);
      return;
    }

    const bannedChannelsList = searchInfo.bannedChannels.split(',').map(channelTitle => channelTitle.trim());
    
    const nHistory = history.slice();
    nHistory.push(searchInfo);
    setHistory(nHistory);
    localStorage.setItem('youtube_history', JSON.stringify(nHistory));
    setSearchInfo({...searchInfo, pageToken: result_list.data.nextPageToken});

    const keys = list.map(datum => datum['id']['videoId']);

    const nList = result_list.data.items
      .filter(item => {
        if (bannedChannelsList.indexOf(item['snippet']['channelTitle']) >= 0)
          return false;

        if (!pageToken)
          return true;

        if (pageToken && keys.indexOf(item['id']['videoId']) >= 0)
          return false;

        return true;
      })
      .map(item => {
        const title = he.decode(item['snippet']['title']);
        return {
          ...item,
          snippet: {
            ...item.snippet,
            title
          }
        };
      });
    if (pageToken)
      setList(list.concat(nList));
    else
      setList(nList);
  }

  return (
    <div className="d-flex">
      <div className="flex-shrink-0 d-none d-sm-block overflow-auto bg-gray text-light" style={{ width: `${16}em`, height: `${100}vh` }}>
        <nav>
          <ul className="nav nav-pills flex-column mb-auto fw-bold">
            {history.map((h, i) => {
              return (
                <li key={i}>
                  <button type="button" className="btn btn-primary w-100" onClick={e => setSearchInfo(h)}>
                    {h.query}
                  </button>
                </li>
              );
            })}            
          </ul>
        </nav>
      </div>
      <div className="container-fluid overflow-scroll" style={{height: "100vh"}}>
        <div className="position-sticky" style={{top: 0}}>
          <div className="d-flex">
            <div>
              <input type="text" className="form-control" 
                value={searchInfo.query} 
                onChange={e => setSearchInfo({...searchInfo, query: e.target.value})} 
                onKeyDown={e => UtilHtml.searchEnter(e, requestSearch)}
              />
            </div>
            <button type="button" className="btn btn-primary"
              onClick={requestSearch}
            >
              {list.length ? 'More' : 'Search'}
            </button>
          </div>
          <div className="d-flex">
            <div>
              <input type="text" className="form-control"
                value={searchInfo.bannedChannels}
                onChange={e => setSearchInfo({ ...searchInfo, bannedChannels: e.target.value })}
              />
            </div>
          </div>
        </div>
        {/* <div style={{height: "64px"}}></div> */}
        <div className="row mt-5">
          {list.map((datum, i, arr) => {
            const videoId = datum['id']['videoId'];
            return (
              <div key={videoId} className="col-3">
                  <div>
                    <a href={`https://www.youtube.com/watch?v=${videoId}`} target="_blank" rel="noreferrer">
                      <img src={datum['snippet']['thumbnails']['medium']['url']} alt={`tmb_${i}`} />
                    </a>
                  </div>
                <div>{datum['snippet']['title']}</div>
                <div className="text-end">
                  <span className="text-danger selectable tooltip" onClick={e => {
                    var nBannedChannels = (searchInfo['bannedChannels']) 
                      ? datum['snippet']['channelTitle'] + ','  + searchInfo['bannedChannels']
                      : datum['snippet']['channelTitle']
                    setSearchInfo({...searchInfo, bannedChannels: nBannedChannels})
                  }}>
                    {datum['snippet']['channelTitle']}
                    <span className="tooltiptext">Ban this Channel</span>
                  </span>
                </div>
                <div className="text-gray-500 text-end" style={{fontSize: `${8}px`}}>
                  {moment(datum['snippet']['publishTime']).format('YY.MM.DD HH:mm')}
                </div>
              </div>
            );
          })}
          
          
        </div>
      </div>
    </div>
  );
}
 
export default VYoutubeSearch;