import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { VLogin, VMain, VRegister, VBubbles, VYoutubeSearch, VTradeHistory } from '../views';


const MainRoutes = (props) => {
  return ( 
    <Switch>
      <Route path="/auth/register"  render={(props) => <VRegister      {...props}/>}/>
      <Route path="/auth/login"     render={(props) => <VLogin         {...props}/>}/>
      <Route path="/d3/bubbles"     render={(props) => <VBubbles       {...props}/>}/>
      <Route path="/youtube-search" render={(props) => <VYoutubeSearch {...props}/>}/>
      <Route path="/trade-history"  render={(props) => <VTradeHistory  {...props}/>}/>
      <Route exact path="/"         render={(props) => <VMain          {...props}/>}/>
      <Route                        render={(props) => <Redirect to="/" />}/>
    </Switch>
  );
}
 
export default MainRoutes;