import moment from "moment";
import React from "react";
import { CartesianGrid, LabelList, Legend, Line, LineChart, ReferenceArea, ReferenceLine, ResponsiveContainer, Tooltip, XAxis, YAxis } from "recharts";
import { UtilCommon, UtilConverter } from "../../../utils";

const _colors_map = {
  "KOSPI"     : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -20),
  "NASDAQ"    : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -20),
  "S&P200"    : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -20),
  "return_of_asset": "#000"
};

const RechartAlpha = (props) => {
  const { data } = props;

  const [_domain, setDomain] = React.useState({top: 'dataMax + 1', bottom: 'dataMin - 1'});
  const [_refArea, setRefArea] = React.useState({left: '', right: ''});

  const [_purified_data, setPurifiedData] = React.useState([]);  

  const _reviseData = React.useCallback((from, to) => {
    if (!data || !data.length)
      return;

    let initial_values = {};

    let purified_data = [];
    let accumulation_of_return_of_asset = 0;

    let is_selected_range = false;
    for (let datum of data) {
      if (datum['date'] === from) {
        is_selected_range = true;
        initial_values = {
          'KOSPI' : datum['KOSPI'],
          'S&P500': datum['S&P500'],
          'NASDAQ': datum['NASDAQ'],
        }
      }

      if (!is_selected_range)
        continue;

      accumulation_of_return_of_asset = (datum['date'] === from) ? 0 : parseFloat((accumulation_of_return_of_asset + datum['return_of_asset']).toFixed(2));
      purified_data.push({
        date: datum['date'],
        'return_of_asset': accumulation_of_return_of_asset,
        'KOSPI' : parseFloat(((datum['KOSPI' ] - initial_values['KOSPI' ]) * 100 / initial_values['KOSPI' ]).toFixed(2)),
        'S&P500': parseFloat(((datum['S&P500'] - initial_values['S&P500']) * 100 / initial_values['S&P500']).toFixed(2)),
        'NASDAQ': parseFloat(((datum['NASDAQ'] - initial_values['NASDAQ']) * 100 / initial_values['NASDAQ']).toFixed(2))
      });

      if (datum['date'] === to)
        break;
    }

    return purified_data;
  }, [data]);


  React.useEffect(() => {
    if (!data || !data.length)
      return;

    const purified_data = _reviseData(data[0]['date'], data[data.length - 1]['date']);
    setPurifiedData(purified_data);
  }, [data, _reviseData]);
  
  if (!data || !data.length)
    return (
      <div className="text-center py-4 text-light" style={{background: '#EDD'}}>
        <h2>NO DATA</h2>
      </div>
    );

  

  function getAxisYDomain(from, to, data) {
    let [bottom, top] = [data[0]['return_of_asset'], data[0]['return_of_asset']];

    let is_selected_range = false;
    for (let datum of data) {
      if (datum['date'] === from)
        is_selected_range = true;

      if (!is_selected_range)
        continue;
      
      if (datum['return_of_asset'] > top) top = datum['return_of_asset'];
      if (datum['return_of_asset'] < bottom) bottom = datum['return_of_asset'];

      if (datum['S&P500'] > top) top = datum['S&P500'];
      if (datum['S&P500'] < bottom) bottom = datum['S&P500'];

      if (datum['NASDAQ'] > top) top = datum['NASDAQ'];
      if (datum['NASDAQ'] < bottom) bottom = datum['NASDAQ'];

      if (datum['KOSPI'] > top) top = datum['KOSPI'];
      if (datum['KOSPI'] < bottom) bottom = datum['KOSPI'];

      if (datum['date'] === to)
        break;
    }
    return [(Math.floor(bottom - 1) | 0), (Math.ceil(top + 1) | 0)];
  };
  
  function zoom() {
    let { left, right } = _refArea;

    if (left === right || right === '') {
      setRefArea({
        left: '',
        right: '',
      });
      return;
    }

    // xAxis domain
    if (left > right) [left, right] = [right, left];
    
    const purified_data = _reviseData(left, right);

    // yAxis domain
    const [bottom, top] = getAxisYDomain(left, right, purified_data);

    setRefArea({
      left: '',
      right: '',
    });

    setDomain({
      bottom,
      top
    });

    setPurifiedData(purified_data);
  }

  function zoomOut() {
    const purified_data = _reviseData(data[0]['date'], data[data.length - 1]['date']);
    setRefArea({left: '', right: ''});
    setDomain({top: 'dataMax + 1', bottom: 'dataMin - 1'});
    setPurifiedData(purified_data);
  }
  



  const AlphaTooltip = ({ active, payload, label }) => {
    if (!active || !payload || !payload.length)
      return null;

    const payload_return_of_asset = payload.filter(payload => payload['name'] === 'Return of Asset')[0];
    const date = moment(label).format('YY.MM.DD');    

    return (
      <div className="bg-light p-2 border rounded" style={{fontSize: `${12}px`, whiteSpace: "pre"}}>
        <p className="fs-5 fw-bold text-center">{date}</p>
        <table>
          <tbody>
            <tr><td colSpan={3}><hr className="my-2" /></td></tr>
            {payload_return_of_asset && (
              <tr>
                <td><p style={{ color: payload_return_of_asset['color'] }}>{`${payload_return_of_asset['name']}`}</p></td>
                <td><p style={{ color: payload_return_of_asset['color'] }}>{` :  `}</p></td>
                <td><code className="w-100" style={{ color: payload_return_of_asset['color'], textAlign: "end", fontSize: `${14}px` }}>
                  {`${UtilConverter.number2commadStringWithIntl(payload_return_of_asset['value']).padStart(8, ' ')}`}
                </code></td>
              </tr>
            )}
            <tr><td colSpan={3}><hr className="my-2" /></td></tr>
            {payload.map((payload, i) => {
              if (payload['name'] === 'Return of Asset')
                return null;

              return (
                <tr key={payload['name']}>
                  <td><p style={{ color: payload['color'] }}>{`${payload['name']}`}</p></td>
                  <td><p style={{ color: payload['color'] }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: payload['color'], textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(payload['value']).padStart(8, ' ')}`}
                  </code></td>
                </tr>
              );
            })}            
          </tbody>
        </table>
      </div>
    );
  };

  return ( 
    <div style={{ userSelect: 'none' }}>
      <div className="px-3 pb-3">
        <button type="button" className="btn btn-outline-dark" onClick={zoomOut}>Zoom Out</button>
      </div>
      <ResponsiveContainer width="100%" height={400}>
          <LineChart
            width={800}
            height={400}
            data={_purified_data}
            onMouseDown={(e) => e && (_refArea.left !== e.activeLabel) && setRefArea({ left: e.activeLabel, right: _refArea['right'] })}
            onMouseMove={(e) => e && _refArea.left && (_refArea.right !== e.activeLabel) && setRefArea({ right: e.activeLabel, left: _refArea['left'] })}
            onMouseUp={zoom}
            margin={{
              top: 40,
              right: 30,
              left: 30,
              bottom: 50,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <Tooltip content={<AlphaTooltip />} />
            <Legend align="left" verticalAlign="top" iconSize={20} wrapperStyle={{top: 0}} />
            <XAxis
              allowDataOverflow
              dataKey="date"
              fontSize={10}
              angle={-40}
              textAnchor="end"
              // verticalAnchor="middle"
              tickSize={4}
              tickMargin={8}
              padding={{ left: 50, right: 50 }}
              tickFormatter={(date) => {
                if (date instanceof Date)
                  return moment(date).format('YY.MM.DD');
                return date;
              }} 
            />
            <YAxis
              allowDataOverflow 
              domain={[_domain['bottom'], _domain['top'  ]]}
              // padding={{ bottom: 50, top: 50 }}
              tickFormatter={(value) => {
                return parseFloat(value.toFixed(2));
              }} 
            />
            <Line
              type="natural" dataKey={'return_of_asset'} name="Return of Asset"
              stroke={_colors_map['return_of_asset']} strokeWidth={2}
              animationDuration={300}
            >
              <LabelList dataKey={'return_of_asset'} offset={10} position="top" fontWeight="bold" />
            </Line>

            <Line type="natural" name={'KOSPI' } dataKey={'KOSPI' } stroke={_colors_map['KOSPI' ]} animationDuration={300} dot={false} />
            <Line type="natural" name={'S&P500'} dataKey={'S&P500'} stroke={_colors_map['S&P500']} animationDuration={300} dot={false} />
            <Line type="natural" name={'NASDAQ'} dataKey={'NASDAQ'} stroke={_colors_map['NASDAQ']} animationDuration={300} dot={false} />
            
            <ReferenceLine y={0} stroke="#000" />

            {_refArea['left'] && _refArea['right'] ? (
              <ReferenceArea x1={_refArea['left']} x2={_refArea['right']} strokeOpacity={0.3} />
            ) : null}
          </LineChart>
        </ResponsiveContainer>
    </div>
  );
}
 
export default RechartAlpha;