import moment from "moment";
import React from "react";
import { CartesianGrid, ComposedChart, Legend, Line, ResponsiveContainer, Scatter, Tooltip, XAxis, YAxis, ZAxis } from "recharts";
import { UtilConverter } from "../../../utils";

const RechartDayTrade = (props) => {
  const { data } = props;

  if (!data || !data.length)
    return (
      <div className="text-center py-4 text-light" style={{background: '#EDD'}}>
        <h2>NO DATA</h2>
      </div>
    );

  // set min, max trade volume
  let min_traded_volume = 100000, max_trade_volume = 0;  
  data.forEach((datum) => {
    if (datum['buy_volume'] < min_traded_volume)
      min_traded_volume = datum['buy_volume'];
    if (datum['sell_volume'] < min_traded_volume)
      min_traded_volume = datum['sell_volume'];
    if (datum['buy_volume'] > max_trade_volume)
      max_trade_volume = datum['buy_volume'];
    if (datum['sell_volume'] > max_trade_volume)
      max_trade_volume = datum['sell_volume'];
  });
  if (!data.length) {
    min_traded_volume = 0;
    max_trade_volume = 100000;
  }




  const AssetRatioTooltip = ({ active, payload, label }) => {
    if (!active || !payload || !payload.length)
      return null;
    
    const full_payload = payload[0]['payload'];

    const date = moment(label).format('YY.MM.DD');

    let payload_price,
        payload_avg_price,
        payload_buy,
        payload_sell;

    payload.forEach(datum => {
      switch (datum['name']) {
        case 'price':
          payload_price = datum;
          break;      
        case 'average price':
          payload_avg_price = datum;
          break;      
        case 'buy':
          payload_buy = datum;
          break;      
        case 'sell':
          payload_sell = datum;
          break;      
        default:
          break;
      }
    });
    if (payload_buy)
      payload_buy['buy_volume'] = full_payload['buy_volume'];
    if (payload_sell)
      payload_sell['sell_volume'] = full_payload['sell_volume'];
    if (payload_avg_price)
      payload_avg_price['count'] = full_payload['count'];


    return (
      <div className="bg-light p-2 border rounded" style={{fontSize: `${12}px`, whiteSpace: "pre"}}>
        <p className="fs-5 fw-bold text-center">{date}</p>
        <table>
          <tbody>
            <tr><td colSpan={3}><hr className="my-2" /></td></tr>
            {payload_price && payload_price['value'] && (
              <tr key={payload_price['name']}>
                <td><p style={{ color: payload_price.color }}>{`${payload_price.name}`}</p></td>
                <td><p style={{ color: payload_price.color }}>{` :  `}</p></td>
                <td><code className="w-100" style={{ color: payload_price.color, textAlign: "end", fontSize: `${14}px` }}>
                  {`${UtilConverter.number2commadStringWithIntl(payload_price['value']).padStart(14, ' ')}`}
                </code></td>
              </tr>
            )}
            {payload_avg_price && payload_avg_price['value'] && payload_avg_price['count'] && (
              <React.Fragment>
                <tr key={payload_avg_price['name']}>
                  <td><p style={{ color: payload_avg_price.color }}>{`${payload_avg_price.name}`}</p></td>
                  <td><p style={{ color: payload_avg_price.color }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: payload_avg_price.color, textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(payload_avg_price['value']).padStart(14, ' ')}`}
                  </code></td>
                </tr>
                <tr key={'count'}>
                  <td><p style={{ color: payload_avg_price.color }}>{'count'}</p></td>
                  <td><p style={{ color: payload_avg_price.color }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: payload_avg_price.color, textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(payload_avg_price['count']).padStart(14, ' ')}`}
                  </code></td>
                </tr>
              </React.Fragment>
            )}
            
            {((payload_buy && payload_buy['value'] && payload_buy['buy_volume']) || (payload_sell && payload_sell['value'] && payload_sell['sell_volume'])) && (
              <tr><td colSpan={3}><hr className="my-2" /></td></tr>
            )}           

            {payload_buy && payload_buy['value'] && payload_buy['buy_volume'] && (
              <React.Fragment>
                <tr key={payload_buy['name']}>
                  <td><p style={{ color: payload_buy.color }}>{'buy_price'}</p></td>
                  <td><p style={{ color: payload_buy.color }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: payload_buy.color, textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(payload_buy['value']).padStart(14, ' ')}`}
                  </code></td>
                </tr>
                <tr key={'buy_volume'}>
                  <td><p style={{ color: payload_buy.color }}>{'buy_volume'}</p></td>
                  <td><p style={{ color: payload_buy.color }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: payload_buy.color, textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(payload_buy['buy_volume']).padStart(14, ' ')}`}
                  </code></td>
                </tr>
              </React.Fragment>
            )}

            {payload_sell && payload_sell['value'] && payload_sell['sell_volume'] && (
              <React.Fragment>
                <tr key={payload_sell['name']}>
                  <td><p style={{ color: payload_sell.color }}>{'sell_price'}</p></td>
                  <td><p style={{ color: payload_sell.color }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: payload_sell.color, textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(payload_sell['value']).padStart(14, ' ')}`}
                  </code></td>
                </tr>
                <tr key={'sell_volume'}>
                  <td><p style={{ color: payload_sell.color }}>{'sell_volume'}</p></td>
                  <td><p style={{ color: payload_sell.color }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: payload_sell.color, textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(payload_sell['sell_volume']).padStart(14, ' ')}`}
                  </code></td>
                </tr>
              </React.Fragment>
            )}            
          </tbody>
        </table>
      </div>
    );
  };


  const CountDot = (props) => {
    const { cx, cy, stroke, payload /*, value*/ } = props;
    let count = payload['count'];
    if (count < 1)
      return;

    let size = Math.min(10, Math.log(count))/10 * 480;
    
    return (
      <svg x={cx - 20} y={cy - 20} width={40} height={40} fill={stroke} fillOpacity={0.2} viewBox="0 0 1024 1024">
        <circle cx={512} cy={512} r={size} />
      </svg>
    );
  };


  return (     
    <ResponsiveContainer width="100%" height={400}>
      <ComposedChart
        syncId="dayTradeChart"
        width={500}
        height={400}
        data={data}
        margin={{
          top: 30,
          left: 30,
          right: 30,
          bottom: 20,
        }}
      >
        <CartesianGrid stroke="#DDD" />
        <Tooltip content={<AssetRatioTooltip />} />
        <Legend align="left" verticalAlign="top" height={36} iconSize={12} />

        <XAxis 
          dataKey="date"
          domain={['auto', 'auto']}
          fontSize={10}
          angle={-40}
          textAnchor="end"
          // verticalAnchor="middle"
          tickSize={4}
          tickMargin={8}
          tickFormatter={(unixTime) => moment(unixTime).format('YY.MM.DD')}
          // label={{ value: 'DATE', position: 'insideBottomRight', offset: 0 }} 
          padding={{ left: 50, right: 50 }}
        />
        <YAxis
          // dataKey="price" 
          // domain={['auto', 'auto']}
          fontSize={10}
          domain={['dataMin', 'dataMax']}
          padding={{ top: 20, bottom: 20 }}
          // unit=""
          // type="number"
          // label={{ value: 'WON', angle: -90, position: 'insideLeft' }}
          tickFormatter={(price) => UtilConverter.number2commadStringWithIntl(price)} 
        />
        <ZAxis dataKey="sell_volume" name="sell_volume" domain={[min_traded_volume, max_trade_volume]} range={[50, 500]} unit="ea" zAxisId="sell_vol" />
        <ZAxis dataKey="buy_volume"  name="buy_volume"  domain={[min_traded_volume, max_trade_volume]} range={[50, 500]} unit="ea" zAxisId="buy_vol" />
        {/* <Scatter name="red" dataKey="red" fill="red" /> */}
        <Line type="natural" dataKey="price" />
        <Line type="natural" name="average price" dataKey="a_price" stroke="#0A0" dot={<CountDot />} />
        <Scatter name="buy"  dataKey="buy_price"  shape="circle" stroke="#F00" fillOpacity={0.5} fill="#F009" legendType="circle" zAxisId="buy_vol"  />
        <Scatter name="sell" dataKey="sell_price" shape="circle" stroke="#00F" fillOpacity={0.5} fill="#00F9" legendType="circle" zAxisId="sell_vol" />
        
        {/* <Line dataKey="redLine" stroke="red" dot={false} activeDot={false} legendType="none" /> */}
      </ComposedChart>
    </ResponsiveContainer>
  );
}
 
export default RechartDayTrade;